  components: {
    LiquidBackground,
    AboutSection,
    ExperienceSection,
    WorksSection,
    ContactSection
  },
  methods: {
    setLocomotiveScroll() {
      new LocomotiveScroll({
        el: this.$refs.container,
        smooth: true,
